.component__wrapper.profile {
    position: relative;
}
.nav__row {
    margin-top: 2.7rem;
}
.nav__row__link {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.9rem;
    letter-spacing: 0.01em;
    color: rgba(106, 112, 126, 0.5);
    margin-right: 2.5rem;
    transition: all 0.3s ease;
}
.nav__row__link.active {
    color: #000000;
}
.nav__row__link:hover {
    color: #000000;
}
.profile__bottom__text {
    position: absolute;
    bottom: 0;
    padding-right: 4.07%;
    left: 2.72%;
    font-weight: 300;
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: #535355;
}
.profile__bottom__text.ident {
    bottom: -16rem;
}
.profile__bottom__text.edit {
    bottom: -4.8rem;
}
.profile__bottom__text a {
    display: inline;
    color: #3E8CE2;
}